import React from 'react'
import NavigateLP from './navigate/navigate'
import { Route, Switch } from 'react-router-dom'
import routePath from '../routepath'
import OverviewLP from './display/overview'
import BlogWritting from './display/blogwrite.js'



export default class LandingPage extends React.Component {

    render() {
        return (
            <div>
                <Route render={({ location }) => location.pathname !== routePath.landBlog
                    ? <LandSite />
                    : <BlogSite />
                } />
            </div>
        )
    }
}

class BlogSite extends React.Component {
    render() {
        return (
            <div>
                <NavigateLP />
            </div>
        )
    }
}

class LandSite extends React.Component {
    componentDidMount(){
        
    }

    render() {
        return (
            <div className="land-side" 
            >
                <Switch>
                    <Route path={routePath.landOvw}>
                        <NavigateLP />
                        <OverviewLP />
                    </Route>
                    <Route path={routePath.landAbout}> </Route>
                </Switch>
            </div>
        )
    }
}