class ExpStage extends React.Component {
    constructor(props) {
        super(props)
        this.turnRef = React.createRef()
        this.ulRef = React.createRef()
        this.animateSection = this.animateSection.bind(this)
        this.reAnimateSection = this.reAnimateSection.bind(this)
    }


    animateSection() {
        this.turnRef.current.classList.add('hover')
    }

    reAnimateSection() {
        this.turnRef.current.classList.remove('hover')
    }
    componentDidMount() {
        this.ulRef.current.innerHTML = this.props.data.Description
    }

    render() {
        let rv = "", right = 'animate__slideInRight', left = 'animate__slideInLeft';
        if (this.props.index % 2 === 0) {
            rv = "flx-reverse";
            right = 'animate__slideInLeft';
            left = 'animate__slideInRight';
        }
        return (
            <div className="exp-stage wow animate__animated animate_rotatequick" data-wow-delay='1s'
                onMouseEnter={this.animateSection}
                onMouseLeave={this.reAnimateSection}

            >
                <div className={'row exp-info ' + rv}>
                    <div className={"exp-section col-md-4 " + rv + " wow " + left} data-wow-delay="0.2s" >
                        <div className={"exp-e-stage exp-circle-item item-" + this.props.index} ref={this.turnRef}>
                            <p>
                                {this.props.data.Stage}
                                <br />
                            </p>
                        </div>

                        <div className="exp-title">
                            <div className="exp-position">
                                {this.props.data.Position}
                            </div>
                            <div className="exp-company">
                                {this.props.data.Company}
                            </div>
                        </div>

                    </div>
                    <div className={"exp-acvm col-md-8 wow " + right} data-wow-delay="0.2s" >
                        {/* <CodeAnimate speed={[40, 0]} execute={[]}> */}
                        <ul ref={this.ulRef}>
                        </ul>
                        {/* </CodeAnimate> */}
                    </div>
                </div>
            </div>
        )
    }
}


