import React from 'react'

export default class turnAnimate extends React.Component {
    constructor(props) {
        super(props)
        this.myTurn = React.createRef();
        this.mouseEnterEvent = this.mouseEnterEvent.bind(this)
        this.mouseLeaveEvent = this.mouseLeaveEvent.bind(this)
        this.tagList = this.props.tagList
        this.condition = true
        // this.isOneTime = false
    }

    analyzePosition() {
        // this.listSpan =[]
        // for(let i = 0; i < this.tagList.length; i++){
        //     let span = document.createElement('span')
        //     span.textContent = "•"
        //     this.pointRef.current.append(span)
        //     this.listSpan.push(this.listSpan)
        // }
    }
    componentDidMount() {
        this.analyzePosition()
    }

    mouseEnterEvent() {
        if (this.condition === true) {
            this.condition = false
            let tvar = null;
            let i = 0, length = this.tagList.length;
            setTimeout(() => {
                let index = 0, varx = null;
                let newInterval = setInterval(() => {
                    varx = document.getElementById(this.tagList[index]);
                    varx.classList.remove('animate__fadeOutLeft')
                    varx.className +=' animate__lightSpeedInRight animated'
                    index++
                    if (index === length) {
                        clearInterval(newInterval)
                        this.condition = true
                    }
                }, 100)
            }, 1000);

            tvar = document.getElementById(this.tagList[i]);
            tvar.classList.remove('animate__lightSpeedInRight')
            tvar.classList.add('animate__fadeOutLeft')
            i++
            let intervals = setInterval(() => {
                tvar = document.getElementById(this.tagList[i]);
                tvar.classList.remove('animate__lightSpeedInRight')
                tvar.classList.add('animate__fadeOutLeft')
                i++
                if (i === length) {
                    clearInterval(intervals)
                }
            }, 100)
        }
    }

    // }


    mouseLeaveEvent() {
        // this.myTurn.current.classList.remove('turning')
        // this.myTurn.current.classList.add("stopturn");
    }

    render() {
        return (
            <div>
                <div
                    onMouseEnter={this.mouseEnterEvent}
                    onMouseLeave={this.mouseLeaveEvent}
                    ref={this.myTurn}
                    className="turn-display"
                >
                </div>
            </div>
        )
    }
}