import React from 'react'
import { Link } from 'react-router-dom'
import './navigatestyle.css'

export default class NavigateLP extends React.Component {
    constructor(props){
        super(props)
        this.headingStatus = false
        this.headingRef = React.createRef()
        this.headBtnRef = React.createRef()
        this.lastScroll = 0
    }

    componentDidMount(){
        this.pageSize = window.innerHeight
        if (window.innerHeight > "768"){
        window.addEventListener('scroll', e => {
            if (this.lastScroll < window.scrollY){
                this.headBtnRef.current.innerHTML = '<i class="fas fa-sort-up"></i>'
                this.headingRef.current.style.transform="rotateX(0deg)"
            }
            else{
                this.headBtnRef.current.innerHTML = '<i class="fas fa-caret-down"></i>'
                this.headingRef.current.style.transform="rotateX(90deg)"
            }
            this.headingStatus = !this.headingStatus
            this.lastScroll = window.scrollY
        })}
    }

    showHeadingMobile() {
        document.getElementById('link-heading').classList.toggle('mobile-show')
    }

    showHeadingPC(evt){
        if(this.headingStatus){
            this.headBtnRef.current.innerHTML = '<i class="fas fa-caret-down"></i>'
            this.headingRef.current.style.transform="rotateX(90deg)"
        }
        else{
            this.headBtnRef.current.innerHTML = '<i class="fas fa-sort-up"></i>'
            this.headingRef.current.style.transform="rotateX(0deg)"
        }
        this.headingStatus = !this.headingStatus
    }

    render() {
        return (
            <div  className="heading-navigate" id="heading-navigate" >
            <div className="navigate-show-heading">
                <button onClick={this.showHeadingPC.bind(this)} className="btn btn-outline-warning" ref={this.headBtnRef}>
                <i className="fas fa-caret-down"></i>
                </button>
            </div>
                <nav ref={this.headingRef} className="scroll-sticky site-header sticky-top py-1 container-fluid" >
                    <div className="navigate-mb-btn d-flex justify-content-center">
                        <button onClick={this.showHeadingMobile} className="btn btn-outline-warning ">
                            <i className="fas fa-ellipsis-v ic-big"></i>
                        </button>
                    </div>
                    <div className="container d-flex flex-column flex-md-row justify-content-between" id="link-heading">
                        <Link className="py-2" to="/" aria-label="Product">
                            <svg xmlns="http://www.w3.org/2000/svg" width={24} height={24} fill="none" stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} className="d-block mx-auto" role="img" viewBox="0 0 24 24" focusable="false"><title>Product</title><circle cx={12} cy={12} r={10} /><path d="M14.31 8l5.74 9.94M9.69 8h11.48M7.38 12l5.74-9.94M9.69 16L3.95 6.06M14.31 16H2.83m13.79-4l-5.74 9.94" /></svg>
                        </Link>
                        <Link className="hid-link py-2" to="/about">About me</Link>
                        <Link className="hid-link py-2" to="/wap">Tutorial</Link>
                        <Link className="hid-link py-2" to="/blog">Blog</Link>

                    </div>
                </nav>

            </div>
        )
    }
}