import React from 'react'

export default class WordWrite extends React.Component {
    constructor(props) {
        super(props)
        this.Ref2This = React.createRef()
        this.preCondition()
        this.listText = this.props.children[0]
    }

    preCondition() {
        if (this.props.children.length > 0) {
            this.object = this.Ref2This.current
        }
        else {
            throw Error('You must provide at least one text.') 
        }
    }

    componentDidMount() {
        this.writeProcess()
    }

    writeProcess() {
        this.object = this.Ref2This.current
        this.writeInterval(0)
    }
    writeInterval(index) {
        let data = this.listText[index], loop = data.length, j = 0;
        let myInterval = setInterval(() => {
            this.object.textContent += data[j]
            j++;
            if (j === loop) {
                clearInterval(myInterval)
                setTimeout(() => {
                    this.removeInterval(index)
                }, this.props.pauseTime)
            }
        }, this.props.speed)
    }

    removeInterval(index) {
        let data = this.listText[index],
        j = data.length - 1;
        let myInterval = setInterval(() => {
            this.object.textContent = this.object.textContent.substring(0, j)
            j--;
            if (j === -1) {
                clearInterval(myInterval)
                if (index === this.listText.length - 1) {
                    index=-1;
                }
                this.writeInterval(index + 1)
            }
        }, this.props.speed)
    }

    render() {
        return (
            <span ref={this.Ref2This}>

            </span>
        )
    }
}