import React from 'react'

export default class CodeAnimate extends React.Component {
    constructor(props) {
        super(props)
        // console.log(this.props.children)
        this.writeCodeFunction = this.writeCodeFunction.bind(this)
        this.myIntervalWriteCode = this.myIntervalWriteCode.bind(this)
        this.myIntervalRemoveCode = this.myIntervalRemoveCode.bind(this)
        this.recursiveWriteCode = this.recursiveWriteCode.bind(this)
        this.codeSpaceNode = React.createRef()
    }

    componentDidMount() {
        this.writeCodeFunction()
    }


    writeCodeFunction() {
        this.write_code = this.codeSpaceNode.current
        // console.log(this.write_code)
        let child_nodes = this.write_code.childNodes
        this.listStep = []
        this.recursiveWriteCode(child_nodes)
        this.myIntervalWriteCode()
    }

    myIntervalWriteCode() {
        let i = 0, j = 0, data = this.listStep[0][1], length = this.listStep.length, subLength = data.length;
        let myWriteInterval = setInterval(() => {
            this.listStep[i][0].textContent += data[j]
            j++
            if (j === subLength) {
                if (i === length - 1) {
                    clearInterval(myWriteInterval)
                    setTimeout(async () => {
                        this.myIntervalRemoveCode()
                    }, 2000)
                }
                else {
                    j = 0
                    i++
                    if(this.listStep[i][0].nodeName === "VAR"){
                        document.querySelector(this.listStep[i][1]).style.display='block'
                        this.listStep[i][0].textContent = '√'
                        i++
                    }
                    data = this.listStep[i][1]
                    subLength = data.length
                }
            }

        }, this.props.speed[0])
    }

    async myIntervalRemoveCode() {
        if (this.props.speed[1] === 0) {
            return;
        }
        else if (this.props.speed[1] < -0) {
            let index = this.listStep.length -1,data = this.listStep[index][1];
            let j = data.length
            let myRemoveInterval = setInterval(()=>{
                this.listStep[index][0].textContent = data.substring(0,j)
                j--
                if (j === 0){
                    index--;j=0;
                    data = this.listStep[index][1]
                }
                if (index === 0) {
                    clearInterval(myRemoveInterval)
                    this.myIntervalWriteCode()
                }
            }, -this.props.speed[1])
        }
        else {
            let index = this.listStep.length - 1
            let myRemoveInterval = setInterval(() => {
                this.listStep[index][0].textContent = ""
                if (index === 0) {
                    clearInterval(myRemoveInterval)
                    this.myIntervalWriteCode()
                }
                index--
            }, this.props.speed[1])
        }
    }

    recursiveWriteCode(listNode) {
        for (let i = 0; i < listNode.length; i++) {
            if (listNode[i].nodeName !== "#text") {
                if(listNode[i].nodeName === "VAR"){
                    this.listStep.push([listNode[i], listNode[i].textContent])
                    listNode[i].textContent=""
                }
                else{
                    this.recursiveWriteCode(listNode[i].childNodes);
                }
            }
            else  {
                this.listStep.push([listNode[i], listNode[i].data])
                listNode[i].data = ""
            }
        }
    }


    render() {
        return (
            <div  ref={this.codeSpaceNode}>
                {this.props.children}
            </div>
        )
    }
}