import { ExceptionMap } from 'antd/lib/result'
import React from 'react'
import { X } from 'react-feather'
import './blog.css'
export default class BlogWritting extends React.Component {
    constructor(props) {
        super(props)
        this.bodyRef = React.createRef()
        this.viewCustomState = {
            heading: false,
            textStyle: false,
            textColor: false
        }
        this.positionTextStyle={
            "bold":0,
            "italic":0,
            "strikethrough":0,
            "subscript":0,
            "superscript":0,
            "underline":0
        }
        this.toolbelRef = {
            bold:React.createRef(),
            italic:React.createRef(),
            strikethrough:React.createRef(),
            subscript:React.createRef(),
            superscript:React.createRef(),
            underline:React.createRef()
        }
        this.headingRef = [React.createRef(), React.createRef()]
        this.textStyleRef = [React.createRef(), React.createRef()]
        this.textColorRef = [React.createRef(), React.createRef()]

        this.map = {};
        this.analyzeKeyDownProcess = this.analyzeKeyDownProcess.bind(this)
        this.analyzeKeyUpProcess = this.analyzeKeyUpProcess.bind(this)
        this.doActionContentBoldText = this.doActionContentBoldText.bind(this)
        this.doActionContentItalicText = this.doActionContentItalicText.bind(this)
        this.savingEditableCursor = this.savingEditableCursor.bind(this)

        this.togglePositionStatus = this.togglePositionStatus.bind(this)
        this.toggleToolBeltStyle = this.toggleToolBeltStyle.bind(this)
        this.toggleLonelyCursor = this.toggleLonelyCursor.bind(this)
        this.toggleHighlightCursor = this.toggleHighlightCursor.bind(this)
        this.hiddenTextStyle = this.hiddenTextStyle.bind(this)

        this.savingTag = [null, null]
        this.editAblePosition = 0
        this.nowSelection = null
        this.nowWorking = null
        this.joinNew = false
    }
    toggleHeading() {
        this.savingEditableCursor()
        if (this.viewCustomState.heading) {
            this.headingRef[1].current.style.display = "none"
        }
        else {
            this.headingRef[1].current.style.display = "inline-block"
        }
        this.headingRef[0].current.classList.toggle("item-focus")
        this.viewCustomState.heading = !this.viewCustomState.heading
    }
    toggleTextStyle() {
        this.savingEditableCursor()
        if (this.viewCustomState.textStyle) {
            this.textStyleRef[1].current.style.display = "none"
        }
        else {
            this.textStyleRef[1].current.style.display = "inline-block"
        }
        this.textStyleRef[0].current.classList.toggle("item-focus")
        this.viewCustomState.textStyle = !this.viewCustomState.textStyle
    }
    hiddenTextStyle() {
        this.textStyleRef[1].current.style.display = "none"
        this.textStyleRef[0].current.classList.remove("item-focus")
        this.viewCustomState.textStyle = false
    }
    toggleTextColor() {
        this.savingEditableCursor()
        if (this.viewCustomState.textColor) {
            this.textColorRef[1].current.style.display = "none"
        }
        else {
            this.textColorRef[1].current.style.display = "inline-block"
        }
        this.textColorRef[0].current.classList.toggle("item-focus")
        this.viewCustomState.textColor = !this.viewCustomState.textColor
    }

    savingEditableCursor() {
        let selection = window.getSelection();
        if (selection.anchorNode && selection.anchorNode == selection.focusNode && selection.anchorOffset == selection.focusOffset) {
            // let node = 
            // let count = 
            let range = document.createRange()
            // range.selectNode(node);
            range.setStart(selection.anchorNode, selection.anchorOffset);
            // range.setEnd(node, count);
            range.collapse(true);
            selection.removeAllRanges();
            selection.addRange(range);
        }
        return selection
    }

    togglePositionStatus(){
        let selection = window.getSelection()
        for (let i in  this.positionTextStyle) this.positionTextStyle[i] = 0
        if (selection.anchorNode === selection.focusNode)
            this.toggleLonelyCursor(selection)
        else
            this.toggleHighlightCursor(selection)
       
        this.toggleToolBeltStyle()
    }

    toggleHighlightCursor(selection){

    }

    toggleLonelyCursor(selection){ 
        let Node = selection.anchorNode.parentNode
        if (Node.nodeName == 'DIV')
            return;
        let node = null, t = this.positionTextStyle
        while (true){
            node = Node.nodeName;
            if (node === "P") break;
            if (node === "STRIKE")
                t.strikethrough = 1
            else if (node === "B")
                t.bold = 1
            else if (node === "U")
                t.underline = 1
            else if (node === "I")
                t.italic = 1
            else if (node === "SUB")
                t.subscript = 1
            else if (node === "SUP")
                t.superscript = 1
            Node = Node.parentNode
        }
    }

    toggleToolBeltStyle(){
        for(let tool in  this.toolbelRef){
            if (this.positionTextStyle[tool] === 1)
                this.toolbelRef[tool].current.classList.add('item-focus')
            else
                this.toolbelRef[tool].current.classList.remove('item-focus')
        }
    }
    getTextStyleOfCluster(selection){
        console.log(selection.anchorNode.closest('p'))

    }


    doActionContentText(action) {
        this.savingEditableCursor()
        let returnBack = document.execCommand(action, false, null)
        if (returnBack){
            this.toolbelRef[action].current.classList.toggle('item-focus')
        }
        this.hiddenTextStyle()
    }


    doActionContentBoldText() {
        this.doActionContentText("bold")
    }

    doActionContentItalicText() {
        this.doActionContentText("italic")
    }
    doActionContentStriketText() {
        this.doActionContentText("strikethrough")
    }
    doActionContentUnderlineText() {
        this.doActionContentText("underline")
    }
    doActionContentSubscriptText() {
        this.doActionContentText("subscript")   
    }
    doActionContentSupscriptText() {
        this.doActionContentText("superscript")
    }

    doActionStrikethroughCMD() {
        document.execCommand('strikethrough')
    }
    doActionUnderlineCMD() {
        document.execCommand('underline')
    }
    doActionSubscriptCMD() {
        document.execCommand('subscript')
    }

    doActionSuperscriptCMD() {
        document.execCommand('superscript')
    }
    doActionBoldCMD() {
        this.toolbelRef.bold.current.classList.toggle("item-focus")
    }
    doActionItalicCMD() {
        this.toolbelRef.italic.current.classList.toggle("item-focus")
    }

    componentDidMount() {
        this.editAble = document.getElementById('container-writting')
        this.nowWorking = document.getElementById('first-pagraph').childNodes[0]
        document.getElementById('container-writting').addEventListener('keydown', evt => {
            this.analyzeKeyDownProcess(evt)
        })
        document.getElementById('container-writting').addEventListener('keyup', evt => {
            this.analyzeKeyUpProcess(evt)
        })
        document.getElementById('container-writting').addEventListener('click', evt => {
            this.togglePositionStatus()
        })
    }

    getCaretPosition(editableDiv) {
        var caretPos = 0,
            sel, range;
        if (window.getSelection) {
            sel = window.getSelection();
            if (sel.rangeCount) {
                range = sel.getRangeAt(0);
                if (range.commonAncestorContainer.parentNode === editableDiv) {
                    caretPos = range.endOffset;
                }
            }
        } else if (document.selection && document.selection.createRange) {
            range = document.selection.createRange();
            if (range.parentElement() === editableDiv) {
                var tempRange = range.duplicate();
                caretPos = tempRange.text.length;
            }
        }
        return caretPos;
    }



    analyzeKeyDownProcess(evt) {
        this.map[evt.keyCode] = (evt.type === 'keydown');
        if (this.map[17] && this.map[66]) {
            this.doActionBoldCMD()
        }
        else if (this.map[16] && this.map[73]) {
            this.doActionItalicCMD()
        }
        else if (this.map[16] && this.map[85]) {
            this.doActionUnderlineCMD()
        }
        else if (this.map[16] && this.map[17] && this.map[220]) {
            document.execCommand('removeFormats')
        }
        else if (this.map[16] && this.map[17] && this.map[83]) {
            this.doActionStrikethroughCMD()
        }
        else if (this.map[16] && this.map[17] && this.map[188]) {
            this.doActionSubscriptCMD()
        }
        else if (this.map[16] && this.map[17] && this.map[190]) {
            this.doActionSuperscriptCMD()
        }
        else {
        }
    }
    analyzeKeyUpProcess(evt) {
        this.map[evt.keyCode] = !(evt.type === 'keyup');
        if (evt.keyCode === 37 || evt.keyCode === 38 || evt.keyCode === 39 || evt.keyCode === 40){
            this.togglePositionStatus()
        }
        else if (evt.keyCode === 8){
            if (this.joinNew){
                this.togglePositionStatus()
                this.joinNew = false
                return;
            }
            
            let anchorNode = window.getSelection().anchorNode
            if (anchorNode != this.nowWorking){
                this.nowWorking = anchorNode
                this.joinNew = true
            }
        }
        else{
            this.joinNew = false
        }
    }


    render() {
        return (
            <div className="b-w-page">
                <div className="b-w-header d-flex">
                    <div className="b-w-logo-class">
                        {/* <img alt="Nothing to show" src="//"></img> */}
                    </div>
                    <div className="b-w-page-toolbelt d-flex align-items-center">
                        <div className="pg-tb-each ">

                            <div className="d-flex">

                                <span className="group-splice"></span>
                                <div className="list-heading-hiden" ref={this.headingRef[1]} style={{ display: "none" }}>

                                    <div className="lst-hidden-rpl" >
                                        <div className="text-class">
                                            <div className="each-lst-show">
                                                <span className="each-cls-lst pale">
                                            <div className="each-icon-bg" ref={this.toolbelRef.bold} >
                                                    Normal text <span className="quick-key">Ctrl+Alt+0</span>
                                            </div>
                                                </span>
                                                <span className="each-cls-lst pale">
                                            <div className="each-icon-bg" ref={this.toolbelRef.bold} >
                                                    <h1>Heading 1</h1> <span className="quick-key">Ctrl+Alt+1</span>
                                            </div>
                                                </span>
                                                <span className="each-cls-lst heading-2 pale">
                                            <div className="each-icon-bg" ref={this.toolbelRef.bold} >
                                                    <h2>Heading 2</h2> <span className="quick-key">Ctrl+Alt+2</span>
                                            </div>
                                                </span>
                                                <span className="each-cls-lst heading-3 pale">
                                            <div className="each-icon-bg" ref={this.toolbelRef.bold} >
                                                    <h3>Heading 3</h3> <span className="quick-key">Ctrl+Alt+2</span>
                                            </div>
                                                </span>
                                                <span className="each-cls-lst heading-4 pale">
                                            <div className="each-icon-bg" ref={this.toolbelRef.bold} >
                                                    <h4>Heading 4</h4> <span className="quick-key">Ctrl+Alt+3</span>
                                            </div>
                                                </span>
                                                <span className="each-cls-lst heading-4 pale">
                                            <div className="each-icon-bg" ref={this.toolbelRef.bold} >
                                                    <h5>Heading 5</h5> <span className="quick-key">Ctrl+Alt+3</span>
                                            </div>
                                                </span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="head-replace-only " onClick={this.toggleHeading.bind(this)}>
                                    <div className="" ref={this.headingRef[0]}>
                                        <span className="head-rp-side">
                                            Heading 1
                                                <span style={{ fontSize: "0.8em", marginLeft: "10px" }}><i className="fas fa-chevron-down"></i>
                                            </span>
                                        </span>
                                    </div>


                                </div>
                                <span className="group-splice"></span>
                            </div>
                        </div>
                        <div className="pg-tb-each ">
                            <div className="d-flex">
                                <div>
                                    <span className="each-group-icon">
                                        <button className="each-icon" onMouseUp={this.doActionContentBoldText}>
                                            <div className="each-icon-bg-head" ref={this.toolbelRef.bold} >
                                                <i className="fas fa-bold"></i>
                                            </div>
                                        </button>
                                        <button className="each-icon" onMouseUp={this.doActionContentItalicText}>
                                            <div className="each-icon-bg-head" ref={this.toolbelRef.italic} >
                                                <i className="fas fa-italic"></i>
                                            </div>
                                        </button>
                                        <button className="each-icon pos-ab" onClick={this.toggleTextStyle.bind(this)}>
                                            <div className="each-icon-bg-head"  ref={this.textStyleRef[0]}>
                                                <i className="fas fa-ellipsis-h"></i>
                                            </div>
                                        </button>
                                    </span>
                                </div>
                                <div className="lst-hidden" ref={this.textStyleRef[1]} style={{ display: "none" }}>
                                    <div className="lst-tool" data-class="text-class">
                                        <div className="each-lst-show">
                                            <button className="each-cls-lst" onClick={this.doActionContentUnderlineText.bind(this)}>
                                                <div className="each-icon-bg pale" ref={this.toolbelRef.underline} >
                                                    Underline <span className="quick-key">Ctrl+U</span>
                                                </div>
                                            </button>
                                            <button className="each-cls-lst" onClick={this.doActionContentStriketText.bind(this)}>
                                                <div className="each-icon-bg pale" ref={this.toolbelRef.strikethrough} >
                                                    Strikethrough <span className="quick-key">Ctrl+Shift+S</span>
                                                </div>
                                            </button>
                                            <button className="each-cls-lst" onClick={this.doActionContentSubscriptText.bind(this)}>
                                                <div className="each-icon-bg pale" ref={this.toolbelRef.subscript} >
                                                    Subcript <span className="quick-key">Ctrl+Shift+&lt;</span>
                                                </div>
                                            </button>
                                                <button className="each-cls-lst"  onClick={this.doActionContentSupscriptText.bind(this)}>
                                                <div className="each-icon-bg pale"  ref={this.toolbelRef.superscript} >
                                                        Superscript <span className="quick-key">Ctrl+Shift+&gt;</span>
                                                </div>
                                                </button>
                                        </div>
                                    </div>
                                </div>
                                <span className="group-splice"></span>
                            </div>
                        </div>
                        <div className="pg-tb-each ">
                            <div className="d-flex">
                                <div>
                                    <span className="each-group-icon">
                                        <span className="each-icon">
                                            <div className="each-icon-bg">
                                                <i className="fas fa-align-left"></i>
                                            </div>
                                        </span>
                                        <span className="each-icon">
                                            <div className="each-icon-bg">
                                                <i className="fas fa-align-center"></i>
                                            </div>
                                        </span>
                                        <span className="each-icon pos-ab">
                                            <div className="each-icon-bg">
                                                <i className="fas fa-align-right"></i>
                                            </div>
                                        </span>
                                    </span>
                                </div>
                                <span className="group-splice"></span>
                            </div>
                        </div>

                        <div className="pg-tb-each ">
                            <div className="d-flex">

                                <div className="list-heading-hiden" ref={this.textColorRef[1]} style={{ display: "none" }}>

                                    <div className="lst-hidden-color">
                                        <div className="text-class">
                                            <div className="each-color-show">
                                                <span className="clr black bg" />
                                                <span className="clr dark-blue bg" />
                                                <span className="clr dark-teal bg" />
                                                <span className="clr dark-green bg" />
                                                <span className="clr dark-orange bg" />
                                                <span className="clr dark-red bg" />
                                                <span className="clr dark-purple bg" />
                                                {/* --------------------- */}
                                                <span className="clr gray bg" />
                                                <span className="clr blue bg" />
                                                <span className="clr teal bg" />
                                                <span className="clr green bg" />
                                                <span className="clr orange bg" />
                                                <span className="clr red bg" />
                                                <span className="clr purple bg" />
                                                {/* ----------------------- */}
                                                <span className="clr white bg" />
                                                <span className="clr light-blue bg" />
                                                <span className="clr light-teal bg" />
                                                <span className="clr light-green bg" />
                                                <span className="clr light-orange bg" />
                                                <span className="clr light-red bg" />
                                                <span className="clr light-purple bg" />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="txt-color-contain" onClick={this.toggleTextColor.bind(this)}>
                                    <div className="txt-color-bg d-flex justify-content-between align-items-center" ref={this.textColorRef[0]}>
                                        <div className="txt-color-tb">
                                            <span className="head">A</span>
                                            <span className="bor"></span>
                                        </div>
                                        <span style={{ fontSize: "0.5em", marginLeft: "10px" }}><i className="fas fa-chevron-down"></i>
                                        </span>
                                    </div>
                                </div>
                                <span className="group-splice"></span>
                            </div>
                        </div>


                        <div className="pg-tb-each ">
                            <div className="d-flex">
                                <div>
                                    <span className="each-group-icon">
                                        <span className="each-icon">
                                            <div className="each-icon-bg">
                                                <i className="far fa-image"></i>
                                            </div>
                                        </span>
                                        <span className="each-icon">
                                            <div className="each-icon-bg">
                                                <i className="fas fa-link"></i>
                                            </div>
                                        </span>
                                        <span className="each-icon pos-ab">
                                            <div className="each-icon-bg">
                                                <svg width={24} height={24} viewBox="0 0 24 27"
                                                    focusable="false" role="presentation"><path d="M5 5h5a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1V6a1 1 0 0 1 1-1zm9 0h5a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1h-5a1 1 0 0 1-1-1V6a1 1 0 0 1 1-1z" fill="currentColor" fillRule="evenodd" /></svg>
                                            </div>
                                        </span>
                                    </span>
                                </div>
                                <span className="group-splice"></span>
                            </div>
                        </div>


                    </div>
                    <div className="b-w-page-action ml-auto">

                    </div>
                </div>
                <div className="b-w-body" >
                    <div className="container b-w-body-writing" id="container-writting" contentEditable={true}
                        suppressContentEditableWarning={true}>
                        <p id="first-pagraph">1</p>

                    </div>
                </div>
            </div>
        )
    }
}

class blogToolBelt extends React.Component{
    
}