export default {
    fixInfoConfig: {
        from: 'middle-middle',
        to: 'top-top',
        direct: true,
        props: {
            '--fonts': {
                from: '5.5em',
                to: '2em'
            },
            '--positionLR': {
                from: '0vw',
                to: '40vw'
            },
            '--opacity': {
                from: '0',
                to: '1'
            },

            '--halfopacity': {
                from: '0.1',
                to: '1'
            }

        }
    },
    right2left: {
        from: 'top-bottom',
        to: 'middle-middle',
        direct: true,
        props: {
            '--lxaxis': {
                from: '1000px',
                to: '0px'
            },
            '--opacity': {
                from: 0,
                to: 1
            },
            '--angle': {
                from: '90deg',
                to: '0deg'
            }
        }
    },
    pageSkew: {
        from: 'top-bottom',
        to: 'middle-middle',
        direct: true,
        props: {
            '--skew': {
                from: '1000px',
                to: '0px'
            },
        }
    },
    turnPage:{
        from: 'top-bottom',
        to: 'middle-middle',
        direct: true,
        props:{
            // '--shadow':{

            // },
            '--yAxes':{
                from:'-50vh',
                to:'0vh'
            }
        }
    }
}